.concentration{
  width: 975px;
  height: 269px;
 
.topConcentration{
  width: 975px;
  height: 37px;
  .cod{
    float: left;
    text-align: center;
    width: 137px;
    height: 37px;
    // border-image: url(../../assets/imgs/标题框.png) 0 0 0 0 fill round;
    p{
      font-size: 16px;
      line-height: 37px;
      letter-spacing: 1px;
      color: #ffffff;
      text-shadow: 0 0 30px rgb(244, 250, 245);
      }
    }
    .qishu{
      float: right;
      width: 180px;
      height: 29px;
    .one{
      margin-top: 10px;
      float: left;
      text-align: center;
      line-height: 29px;
      font-size: 14px;
      letter-spacing: 1px;
      color: #00ffff;
      width: 88px;
      height: 29px;
      // border-image: url(../../assets/imgs/出口COD-TAB点击.png) 0 0 0 0 fill round;
    }
    .three{
      margin-top: 10px;
      float: right;
      width: 88px;
      height: 29px;
      text-align: center;
      line-height: 29px;
      font-size: 14px;
      letter-spacing: 1px;
      color: #42b6fe;
      // border-image: url(../../assets/imgs/出口COD-TAB.png) 0 0 0 0 fill round;
      }
    }
   }
  }
  .districtRow{
    width: 100%;
    height: 222px;
    margin-top: 10px;
    // border-image: url(../../assets/imgs/框.png) 1 0 0 0 fill round;
    border-image-repeat: stretch;
    overflow: hidden;
    .oneBox:nth-child(1){
      margin: 25px 0 0 47px;
      border: 1px dashed #0090ff;
    }
    .oneBox:nth-child(2){
      margin: 25px 0 0 30px;
      border: 1px dashed #0090ff;
      border-style:dashed none dashed dashed; 
    }
    .oneBox:nth-child(3){
     
      border: 1px dashed #0090ff;
      border-style:dashed none dashed none; 
    }
    .oneBox:nth-child(4){
     
      border: 1px dashed #0090ff;
      border-style:dashed none dashed none; 
    }
    .oneBox:nth-child(5){
     
      border: 1px dashed #0090ff;
      border-style:dashed dashed dashed none; 
    }
    .oneBox{
      display:inline-block;
      position: relative;
      width: 172px;
      height: 180px;
      // margin: 50px 0 0 40px;
      .codCircle{
        position: absolute;
        top: 30px;
        left: 22px;
      }
      p{
        // letter-spacing: 2px;
        position: absolute;
        font-size: 14px;
        color: #ffffff;
      }
      span{
        position: absolute;
        left: -2%;
        top: 40%;
        font-size: 16px;
        color: #0090ff;
        letter-spacing: 2px;
      }
    }
  }
.spanA {
  position: absolute;
  top: 90% !important;
  left: 35% !important;
  margin-top: -13px;
}
.spanANew {
  position: absolute;
  top: 90% !important;
  left: 0 !important;
  margin-top: -13px;
  width: 100%;
  text-align: center;
}
.spanAA{
  position: absolute;
  top: 50% !important;
  left: 35% !important;
}
.spanAAA{
  position: absolute;
  top: 40% !important;
  left: 30% !important;
}
